import { Col, Drawer, Row, Button } from 'antd';
import React, { Component } from 'react';
import { DragDropContext } from 'react-beautiful-dnd';
import { connect } from 'react-redux';
import PageLayout from '../Core/PageLayout.js';
import LocationCarousel from '../LocationCarousel';
import ResultList from '../Results/ResultList.js';
import { addToPublicIndex, getPublicQueryResults } from './../../store/actions/queryActions';
import { clearItineraryInView } from './../../store/actions/viewActions';
import ItineraryViewer from './../ItineraryViewer';
import { withFirestore } from 'react-redux-firebase'
import { compose } from 'redux';
import { content } from './Dashboard.module.scss';
import ResizeContainer from '../Utilities/ResizeContainer';

class Dashboard extends Component {
    state = {
        collapsed: false,
        component: 1,
        itineraries: []
    }

    onCollapse = (collapsed) => {
        this.setState({ collapsed });
    }

    onMenuClick = (comp) => {
        this.setState({ component: comp });
    }

    loadMore() {
        if (this.state.next) {
            let itineraries = [...this.state.itineraries]
            this.state.next.get().then((querySnapshot) => {
                querySnapshot.forEach((doc) => {
                    itineraries.push({ ...doc.data(), id: doc.id })
                });
                let next = querySnapshot.docs.length >= 15 ? this.props.firestore.collection('publicItineraries').orderBy('meta.addedDate', 'desc').startAfter(querySnapshot).limit(15) : null
                this.setState({ itineraries: itineraries, next });
            })
        }
    }

    componentDidMount() {
        let itineraries = []
        let ref = this.props.firestore.collection('publicItineraries').orderBy('meta.addedDate', 'desc').limit(15).get().then((querySnapshot) => {
            querySnapshot.forEach((doc) => {
                itineraries.push({ ...doc.data(), id: doc.id })
            });
            let next = querySnapshot.docs.length >= 15 ? this.props.firestore.collection('publicItineraries').orderBy('meta.addedDate', 'desc').startAfter(querySnapshot).limit(15) : null
            this.setState({ itineraries: itineraries, next });
        })
    }

    render() {
        console.log(this.state)
        return (
            <PageLayout>
                <div className={content}>
                    <Row type="flex" justify="center">
                        <Col xs={22} sm={22} md={22} lg={20} xl={20} xxl={20}>
                            <ResizeContainer>
                                <h2>Trending Locations</h2>
                                <LocationCarousel />
                                <h2>Newest Itineraries</h2>
                                <ResultList itineraries={this.state.itineraries} />
                                {
                                    this.state.next && <Button type="primary" onClick={this.loadMore}>Load More</Button>
                                }
                            </ResizeContainer>
                        </Col>
                    </Row>
                </div>
                <Drawer
                    placement="right"
                    closable={false}
                    onClose={this.onClose}
                    visible={this.props.itineraryInFocus}
                    width={window.innerWidth <= 760 ? "100vw" : "50vw"}
                >
                    <DragDropContext onDragEnd={() => { }} >
                        <ItineraryViewer dragging={this.props.dragging} match={this.props.match} onBack={() => this.props.clearItineraryInView()}></ItineraryViewer>
                    </DragDropContext>
                </Drawer>
            </PageLayout>
        )
    }
}


const mapDispatchToProps = (dispatch) => {
    return {
        addToPublicIndex: (newItin) => dispatch(addToPublicIndex(newItin)),
        getPublicQueryResults: (coordinates) => dispatch(getPublicQueryResults(coordinates)),
        clearItineraryInView: () => dispatch(clearItineraryInView())
    }
}

const mapStateToProps = (state) => ({
    itineraryInFocus: state.view.itinerary,
})


export default compose(withFirestore, connect(mapStateToProps, mapDispatchToProps))(Dashboard);